@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);


body {
  align-items: stretch;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  font-family: Poppins, sans-serif;
  margin: 0;
}

textarea:focus,
input:focus,
button:focus {
  outline: none;
}

button:active {
  background-color: #181823;
}


.header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 0 4em 0 4em;
  }
  
  .header-menu {
    align-items: center;
    display: flex;
    margin: 0;
  }
  
  .header-menu-itens {
    font-weight: 300;
    margin: 0 1.6em 0 1.6em;
    text-decoration: none;
    color: white;
  }
  
  .contact-menu {
    background-color: purple;
    font-weight: 600;
    margin: 0;
    padding: 1.2em;
  }
  

.presentation {
    display: flex;
    justify-content: space-around;
    margin: 3em;
  }
  
  .presentation-img-container {
    align-items: center;
    display: flex;
    width: 70%;
  }
  
  .presentation-img {
    max-width: 100%;
    position: absolute;
  }
  
  .presentation-container {
    background: rgba(24, 24, 35, 0.75);
    display: flex;
    flex-direction: column;
    height: 50em;
    justify-content: center;
    padding-left: 2em;
    position: relative;
    width: 100%;
  }
  
  .presentation-text {
    font-size: clamp(8px, 4.8vw, 48px);
    font-weight: 700;
    margin: 1em;
    max-height: 2em;
  }
  
  .presentation-text-frontend {
    color: rgba(255, 255, 255, 0.5);
    margin-top: 0;
  }

.experience-container {
    display: flex;
    flex-direction: column;
    margin: 10em;
  }
  
  .experience-tag {
    color: #8491a0;
    font-size: clamp(12px, 1vw, 16px);
    font-weight: 300;
    text-transform: uppercase;
  }
  
  .experience-briefing {
    font-size: clamp(26px, 3.6vw, 48px);
    max-width: 16em;
  }
  
  .experience-list {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .experience-info {
    display: flex;
    flex-direction: column;
    padding: 0.4em;
  }
  
  .experience-info-id {
    color: #8491a0;
    font-size: clamp(72px, 6vw, 180px);
    font-weight: 400;
    height: 1.2em;
    margin: 0;
  }
  
  .experience-info-title {
    align-items: flex-start;
    display: flex;
    font-size: clamp(24px, 2vw, 32px);
    height: 3em;
    width: 360px;
  }
  
  .experience-info-text {
    color: #8491a0;
    font-size: clamp(16px, 1vw, 20px);
    line-height: 1.8em;
    letter-spacing: 0.1em;
    max-width: 23.6em;
  }

.value-container {
  align-self: flex-end;
  background: #584732;
  display: flex;
  height: 32em;
  justify-content: space-between;
  margin-top: 8em;
  padding-left: 1.5em;
}

.value-text-container {
  align-self: center;
  display: flex;
  flex-direction: column;
  margin-left: 4em;
}

.value-text-title {
  font-size: clamp(24px, 4vw, 72px);
  font-weight: 800;
  margin: 0;
}

.value-text-body {
  font-size: clamp(12px, 1vw, 22px);
  font-weight: 300;
  margin-top: 48px;
  max-width: 32em;
}

.value-text-link {
  color: #d6dee2;
  font-size: clamp(12px, 1.1vw, 22px);
  line-height: 150%;
  margin-top: 8px;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.value-img {
  align-self: baseline;
  height: 32em;
}

.refer-img {
  display: none;
}


.skills-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 4em 2em 0 2em;
  }
  
  .skills-introduction {
    align-self: center;
  }
  
  .skills-introduction-title {
    font-size: clamp(48px, 2.6vw, 96px);
    font-weight: 700;
    margin-top: 0;
  }
  
  .skills-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 2000px;
  }
  
  .skill-title {
    margin: 0.6em 0 0.6em 0;
  }
  
  .skill-img {
    height: 3em;
  }
  
  .skill-text {
    color: #8491a0;
  }
  
  .skill-container {
    max-width: 320px;
    padding: 2em 2em 0 2em;
  }

.jobs-list-container {
    display: flex;
    flex-direction: column;
    margin-top: 7.2em;
  }
  
  .jobs-list-texts {
    margin-left: 108px;
    text-align: start;
  }
  
  .jobs-list-title {
    color: #8491a0;
    font-size: 1vw;
    font-weight: 300;
    margin: 0;
  }
  
  .jobs-list-text {
    font-size: 2.4vw;
    font-weight: 700;
    margin: 0;
    max-width: 600px;
  }
  
  .jobs-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 16px 32px 0 32px;
  }
  
  .link {
    text-decoration: none;
  }
  
  .card-job {
    background-color: #181823;
    color: white;
    display: flex;
    -webkit-filter: drop-shadow(-4px -4px 10px rgba(24, 24, 35, 0.4));
            filter: drop-shadow(-4px -4px 10px rgba(24, 24, 35, 0.4));
    flex-direction: column;
    margin-top: 32px;
    width: 32em;
  }
  
  .card-job-img {
    width: 100%;
  }
  
  .card-job-texts-container {
    padding: 1.6em;
  }
  
  .jobs-list-soon {
    align-self: center;
    border: 1px solid #5221e6;
    margin-top: 48px;
    padding: 16px;
    text-align: center;
    width: 213px;
  }
  

.github-container {
    align-self: flex-end;
    background: white;
    color: #000;
    display: flex;
    height: 32em;
    justify-content: space-between;
    margin-top: 4em;
    padding-left: 1.5em;
  }
  
  .github-text-container {
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-left: 4em;
  }
  
  .github-text-title {
    font-size: clamp(24px, 4vw, 72px);
    font-weight: 800;
    margin: 0;
  }
  
  .github-text-body {
    font-size: clamp(12px, 1vw, 22px);
    font-weight: 300;
    margin-top: 48px;
    max-width: 32em;
  }
  
  .github-text-link {
    color: #181823;
    font-size: clamp(12px, 1.1vw, 22px);
    line-height: 150%;
    margin-top: 8px;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
  }
  
  .github-img {
    align-self: baseline;
    height: 32em;
    margin-right: 5em;
  }

.contact-form-container {
    display: flex;
    justify-content: space-around;
    margin: 128px 0 64px 0;
    padding: 0 12em 0 12em;
    flex-wrap: wrap;
  }
  
  .contact-form-title {
    font-size: clamp(56px, 1vw, 64px);
    margin-bottom: 0;
  }
  
  .contact-form-text {
    font-size: clamp(14px, 1vw, 20px);
    line-height: 150%;
    color: #8491a0;
    width: 30em;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
  }
  
  .contact-form-label {
    display: flex;
    flex-direction: column;
    margin-top: 36px;
  }
  
  .contact-form-input {
    background-color: #181823;
    color: white;
    margin-top: 16px;
    border: none;
    height: 50px;
    width: 30em;
    padding-left: 1em;
    font-family: Poppins, sans-serif;
  }
  
  .contact-form-textarea {
    background-color: #181823;
    border: none;
    color: white;
    margin-top: 16px;
    height: 100px;
    padding: 1em;
    font-family: Poppins, sans-serif;
  }
  
  .contact-form-button {
    margin-top: 32px;
    border: none;
    background-color: #5221e6;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    color: white;
    line-height: 150%;
    padding: 16px 0 16px;
  }

.footer-container {
    background-color: #181823;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 420px;
  }
  
  .footer-leftside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 320px;
  }
  
  .footer-icons {
    margin-right: 1em;
  }
  
  .footer-text {
    color: #8491A0;
    font-weight: 200;
  }
  
  .footer-rightside {
    display: flex;
    flex-direction: column;
  }
  
  .footer-menu-itens {
    text-decoration: none;
    color: white;
    margin-top: 16px;
  }
